export default {
	data() {
		return {
			// 
			share: {
				title: '',
				path: '/pages/index/index', //  
				imageUrl: '', // ()
				desc:"",
				content:""
			}
		}
	},
	// 
	// 1.
	onShareAppMessage(res) {
		// console.log("res ==>",res);
		let _this = this;
		// let pages = getCurrentPages();
		// let view = pages[pages.lengh - 1]
		// console.log("pages ==>",pages);
		return {
			title: _this.share.title,
			path: _this.share.path,
			imageUrl: _this.share.imageUrl,
			desc:_this.desc,
			content:_this.content,
			success(res) {
				uni.showToast({
					title: ''
				})
			},
			fail(res) {
				uni.showToast({
					title: '',
					icon: 'none'
				})
			}
		}
	},
	//2.
	onShareTimeline(res) {
		let _this = this;
		return {
			title: _this.share.title,
			path: _this.share.path,
			imageUrl: _this.share.imageUrl,
			desc:_this.desc,
			content:_this.content,
			success(res) {
				uni.showToast({
					title: ''
				})
			},
			fail(res) {
				uni.showToast({
					title: '',
					icon: 'none'
				})
			}
		}
	},
}
